export default function MembersPaymentReminderButtonCtrl($scope, $state, sweet, Api) {
  // Action functions start
  $scope.sendPaymentReminders = function (method, force) {
    const members = [];

    if ($state.current.name == 'members.detail') {
      members.push($scope.member.id);
    } else {
      for (let i = 0; i < $scope.members.data.length; i++) {
        if ($scope.members.data[i].selected == true) {
          members.push($scope.members.data[i].id);
        }
      }
    }

    Api.PaymentReminders().save({ members, method, force }, (data) => {
      sweet.show('Sdr!', 'D var pminnelserna postade! Bon apetit!', 'success');
    }, (response) => {
      sweet.show(
        {
          title: `${response.status} Ops!`,
          text: response.data.message,
          confirmButtonText: 'Ja, tvinga ivg ytterligare en pminnelse',
          showCancelButton: true,
          cancelButtonText: 'Okej, d vill jag inte skicka.',
        },
        (didConfirm) => {
          if (didConfirm) {
            $scope.sendPaymentReminders(method, true);
          }
        },
      );
      console.log('response', response);
    });
  };

  $scope.sendPaymentRemindersInfo = function () {
    sweet.show({
      title: 'Skicka masspminnelser!',
      text: '<strong>Skicka med automatiskt valt medium:</strong> Pminnelser skickas i frsta hand genom sms (om medlemmen angivit telefonnummer) och i andra hand genom e-post. <br />' +
      '<strong>Skicka med sms:</strong> Pminnelser skickas enbart till markerade medlemmar med angivet mobilnummer. <br />' +
      '<strong>Skicka med e-post:</strong> Pminnelser skickas enbart till markerade medlemmar med angiven e-postadress',
      html: true,
      type: 'info',
    });
  };
}
